.myself {
    margin-top: 450px;
    max-width: 90%;
    margin-left: 10%;
    @media screen and ( max-width:1080px) {
      margin-top: 150px;
      }
      @media screen and (max-width:500px) {
        margin-top: 0;
            margin-left: 0;
      }


h2 {
    font-family: "tusker";
    font-size: 80px;
    line-height: 1rem;
    display: block;
    position: relative;
    @media screen and (max-width:500px) {
        font-size: 60px;
      }
  
}
.namen {
    font-size: 16px;
    
}


.flex {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
    @media screen and ( max-width:1080px) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
}
p {
    font-size: 2rem;
    
        @media screen and (max-width:500px) {
            font-size: 15px;
            line-height: 20px;
            margin-bottom: 20px;
        }
    
    
}
    .description {
       font-size: 2rem;
       max-width: 40vw;
       @media screen and ( max-width:1080px) {
        max-width: 100%;
      }
      @media screen and (max-width:500px) {
            margin-bottom: 50px;
      }
    }

    video {
        z-index: 7;
        width: 100%;
        @media screen and (max-width:500px) {
            margin: 20px 0;
          
          }

    }
   
}

.corona {
    position: relative;
    max-width: 300px;
    margin-left: 50px;
    @media screen and (max-width:500px) {
       margin: 20px 0;
      
      }
    p {
        line-height: 1em;
    }
   
    .relative {
        position: relative;
    }
    #img {
       position: absolute;
        width: 86px;
        top: -4px;
        right: -10px;
        @media screen and (max-width:500px) {
            width: 50px;
           }
        
    }
    #fleche {
        position: absolute;
        width: 120px;
        top: -30px;
        right: -70px;
        @media screen and (max-width:500px) {
            width: 86px;
           }
    }
}