.connect {
    border-bottom: 1px solid white;
    padding-bottom: 20px;
    h2 {
        font-family: "tusker";
        line-height: 1em;
        font-size: 218px;
        font-weight: 100;
        padding: 0;
        margin: 0;
        color: #fbf3e9;
        @media screen and (max-width:500px) {
            font-size: 80px;
        }
    }
    p {
        font-size: 28px;
        line-height: 40px;
        @media screen and (max-width:500px) {
            font-size: 15px;
            line-height: 20px;
        }
    }
    .button-container {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 100px;
        @media screen and (max-width:500px) {
            margin-bottom: 40px;
        
        }
        

        .contact-me {
            margin: 8px 0;
            margin-right: 6px;
            color: #fbf3e9;
            @media screen and (max-width:500px) {
                margin: 3px 0;
                padding-top: 6px;
            }
           a {
            @media screen and (max-width:500px) {
                font-size: 18px;
            }
           }

        }
    }
    .flex {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        @media screen and (max-width:500px) {
            flex-direction: column;
            align-items: flex-start;
        }

        .contact-me-inverse{
            margin-left: 6px;
            color: #fbf3e9;
            padding: 10px;
            @media screen and (max-width:500px) {
               padding-left: 0;
            }
            a {
            @media screen and (max-width:500px) {
                font-size: 18px;
            }
        }

        }
    }
  
}